.navigation {
	position: fixed;
	width: 150px;
	height: 200px;
	bottom: 10px;
	left: 10px;
	background-color: #d28644;
	border: double 5px #674427;
	text-align: left;
	display: flex;
	flex-direction: column;
	padding: 5px;
	z-index: 1000;
}

.navLink {
	border-bottom: 1px dotted #ffffff;
	display: flex;
	flex-direction: row;
	align-items: center;
	padding: 5px;
}

.navLink a {
	margin-left: 10px;
	color: #ffffff;
}